@keyframes fadeInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(3rem);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes formIncrease {
  0% {
    padding-left: calc(100% - 31rem);
  }

  100% {
    padding-left: 0;
  }
}

@keyframes social-block__toggler {
  0% {
    height: 0;
    width: 0;
  }

  50% {
    height: 112.5%;
    width: 112.5%;
  }

  100% {
    height: 100%;
    width: 100%;
  }
}

@keyframes screenFill {
  100% {
    transform: translateY(0);
  }
}

@keyframes rulesItemNumerics {
  100% {
    transform: scale(1);
  }
}

@keyframes rulesGoBtn {
  100% {
    width: 100%;
  }
}

@keyframes rulesGoBtnText {
  100% {
    color: #5f458c;
  }
}
